import React from "react"
import Header from "./Header";
import Footer from "./Footer";
import './timelinestyle.css'

const Timeline = () => {
  return (  
    <div>
    <Header/>
    <main class="l-main">
    <section class="about section " id="about">
    <h2 class="section-title">Experience</h2>
    <div class="timeline">
      <div class="container left">
        <div class="content">
          <h2>Google Summer of Code '21</h2>
          <p>OpenPrinting (The Linux Foundation)</p>
          <p>Primary focus of the project was to convert filters (pdftoraster, texttopdf, bannertopdf) into filter functions. These functions would be integrated in a shared library so as to reduce the number of calls to individual filter executables and minimize CPU resources exploitation.</p>
        </div>
      </div>
      <div class="container right">
      <div class="content">
      <h2>Core Research Member</h2>
      <p>Swachta Mitra | Feb'21</p>
      <p>Worked on an Open Research Problem of Image Classification and Segmentation.Trained numerous models like Inceptionv3, VGG16 and ResNet and optimized
      them using progressive resizing, Stratified K-fold and ensembling techniques</p>
    </div>
    </div>
      <div class="container left">
        <div class="content">
        <h2>Machine Learning Intern</h2>
        <p>Trariti Consulting Group | June'20</p>
        <p>Developed a ML tool from scratch to detect the big 5 personality traits using facial cues. Implemented FaceTube Research Paper to evaluate the facial cues. Acheived above 90% accuracy.</p>
      </div>
        </div>
      
    </div>
    </section>
    </main>
    <Footer/>
    </div>
  )
}
export default Timeline;